
import { Button } from "reactstrap"


const RadioGroup = ({ id, options, isSelected, setSelected }) => {

    const getLabel = (option) => !!option.abbrLabel ? option.abbrLabel : option.label

    const changeHandler = (event, option) => {
        const { name, value, checked} = event.target
        // console.log(`name: ${name}, value ${value} checked: ${checked}`)
        setSelected(name, value, checked)
    }
    const clearHandler = () => {
        // console.log(`clearHandler(${id})`)
        setSelected(id, "", true)
    }

    return (
        <>
        <br />
        <span>
        {options.map(opt => (
            <label
                key={opt.id}
                className="prompt-option"
            >
                <input
                    type="radio"
                    className="prompt-radio-btn"
                    name={id}
                    value={opt.id}
                    checked={isSelected(id, opt.id)}
                    onChange={(event) => changeHandler(event, opt)}
                />
                {getLabel(opt)}
            </label>
        ))}
            <Button
                color="primary"
                size="sm"
                outline={true}
                onClick={clearHandler}
                className="clear-radio-btn"
            >
                Clear
            </Button >
        </span>
        </>
    )

}

export default RadioGroup